<template>
  <div class="column is-3-desktop is-4-tablet is-full-mobile">
    <router-link tag="div" class="box" :to="`/languages/${slug}`">
      <span class="title is-6">{{ name }}</span>
      <i v-for="country in countries" :key="country">{{ country }} </i>
      <br>
      <p v-if="translatedWordCount > 0">
        {{ translatedWordCount }} / {{ wordCount }} words translated
      </p>
      <p v-else>
        No translated words
      </p>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'language-item',
  props: {
    name: String,
    slug: String,
    countries: Array,
    translatedWordCount: Number,
    wordCount: Number,
  },
};
</script>

<style scoped>
i {
  font-style: normal;
}
.box {
  border-top: 4px solid #3A4541;
  background-color: #F1E685;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  cursor: pointer;
  min-height: 7rem !important;
}
.box:hover {
  box-shadow: 0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07);
}
</style>



