<template>
  <div>
    <h1 class="title">Languages</h1>
    <div class="columns is-multiline">
      <language-item
        v-for="{ name, slug, countries } in languages"
        :key="slug"
        :name="name"
        :slug="slug"
        :countries="countries"
        :translatedWordCount="getTranslatedWordCount(slug)"
        :wordCount="wordCount"
      >
      </language-item>
    </div>
    <button class="button to-top" v-scroll-to="'#app'">☝️ Scroll to top</button>
  </div>
</template>

<script>
import {
  getLanguages,
  getTranslatedWordCount,
  getWordCount,
} from '@/api';

import LanguageItem from '@/components/LanguageItem';

export default {
  name: 'languages',
  components: {
    LanguageItem,
  },
  data: () => ({
    languages: [],
    wordCount: 0,
  }),
  created() {
    this.languages = getLanguages();
    this.wordCount = getWordCount();
  },
  methods: {
    getTranslatedWordCount,
  },
};
</script>

<style scoped>
.to-top {
  height: 2.25rem;
  line-height: 2.25rem;
  padding: 0 0.625rem;
  margin-top: 1rem;
  width: 100%;
}
</style>



